<template>
	<div>
		<div class="secction diyscrollbar">
			<list-top placeholder="输入课程名查找" btnText="添加课程" v-on:addBtn="addCourse" v-on:search="searchCourseAction"></list-top>
			<courses-list v-on:setRows="setRows" v-bind:totalCourses="totalCourses" v-bind:currentPage="currentPage"  v-bind:child-courses-list='coursesList' v-on:sonDelCourse="fatherDelCourse" v-on:childGetCourses="getCourses" v-on:childAmendCourse="fatherAmendCourse"></courses-list>
		</div>
		<el-dialog v-bind:title="dialog.title" v-model="dialog.control" size="small">
			<el-form ref="dialog.form" :model="dialog.form" label-width="80px">
				<el-form-item label="课程名">
					<el-input v-model="dialog.form.name"></el-input>
				</el-form-item>
				<el-form-item label="课程简介">
					<el-input type="textarea" :rows="3" v-model="dialog.form.description"></el-input>
				</el-form-item>
				<el-form-item label="课程提示">
					<el-input type="textarea" :rows="3" v-model="dialog.form.prompt"></el-input>
				</el-form-item>
				<el-form-item label="课程图片">
					<el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
						<img v-if="dialog.form.imgUrl" :src="dialog.form.imgUrl" class="avatar">
						<i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
    			<el-button @click="dialog.control = false">取 消</el-button>
    			<el-button type="primary" @click="onSubmit">确 定</el-button>
 			 </span>
		</el-dialog>
	</div>
</template>

<script>
	import coursesList from './classInfo/CoursesList.vue'
	import { Dialog, Form, Button, Upload, Tag } from 'element-ui'
	import coursesUrl from '../../../static/url/Course.js'
	import listTop from './public/ListTop.vue'
	export default {
		name: 'classinfo',
		components: {
			coursesList,
			'el-dialog': Dialog,
			'el-form': Form,
			'el-button': Button,
			'el-upload': Upload,
			'el-tag': Tag,
			'list-top': listTop,
		},
		data() {
			return {
				dialog: {
					title: '',
					control: false, //控制dialog的显示与关闭
					uploadImgUrl: coursesUrl.uploadImgUrl,
					form: {
						name: '', //课程名
						description: '', //课程简介
						prompt: '', //课程提示
						imgUrl: '', //课程图片
					},
				},
				currentPage: 1,//当前页
				rows:30,//每页条数
				totalCourses:0,//总条数
				coursesList: [],
				currentNum:0,//每页多少条
			}
		},
		created() {
			this.getCourses(30, '');
		},
		methods: {
			setRows(num){
				this.rows = num;
			},
			getCourses(currentNum) { //获取数据
				this.currentNum = currentNum;
				//searchKey = searchKey == 'undefined' ? '' : searchKey;
				/*this.$http.get(coursesUrl.getCourse).then((response) => {
					console.log("所有课程dddd");
					console.log(response.data)
					
					this.coursesList = response.data;
					
				}, (response) => {
					this.$message.error('获取课程失败');
				});*/

				/*console.log("获取课程kkkk---"+(currentNum/this.rows));
				this.$http.get("/admin/course/list?page="+currentNum+"&rows="+this.rows).then((response) => {
					console.log(response.data)
					this.coursesList = response.data.data.content;
					this.totalCourses = response.data.data.totalElements;
					
				}, (response) => {
					this.$message.error('获取课程失败');
				});
				//console.log("获取课程kkkk---"+(currentNum/this.rows));
				*/
				this.$http.get("/admin/course/getAll").then((response) => {
					console.log(response.data)
					this.coursesList = response.data.data;
				}, (response) => {
					this.$message.error('获取课程失败');
				});
			},
			addCourse() { //显示添加的dialog
				this.dialog.form = {
					name: '', //课程名
					description: '', //课程简介
					prompt: '', //课程提示
					imgUrl: '', //课程图片
				};
				this.dialog.control = true;
				this.dialog.title = "添加课程";
			},
			fatherAmendCourse(course) { //显示修改的dialog
				this.dialog.form = JSON.parse(JSON.stringify(course));
				this.dialog.control = true;
				this.dialog.title = "修改课程";
			},
			fatherDelCourse(id) {
				this.$confirm('此操作将永久删除该课程, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					console.log("删课程aaaa" + id);
					this.$http.delete("/admin/course/" + id).then((response) => {
						if(response.data.data) {
							/*for(let i = 0; i < this.coursesList.length; i++) {
								if(this.coursesList[i].id == id) {
									this.coursesList.splice(i, 1);
									this.$message.success('删除成功');
								}
							}*/
							this.$message.success('删除成功');
							this.getCourses(this.currentNum);
						} else {
							this.$message.error('删除失败');
						}

					}, (response) => {
						this.$message.error('链接失败');
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			searchCourseAction(keyWord) { //搜索按钮
				this.getCourses(30, key);
			},
			onSubmit() { //处理添加或修改课程
				if(!this.dialog.form.id) { //添加课程
					console.log("添加课程bbbb");
					console.log(JSON.parse(JSON.stringify(this.dialog.form)));
					this.$http.post("/admin/course",
						JSON.parse(JSON.stringify(this.dialog.form))
					).then((response) => {
						if(response.data.status == 200) {
							this.dialog.control = false;
							this.$message.success('添加成功');
							this.getCourses(this.currentNum);
						} else {
							this.$message.error('添加失败');
						}

					}, (response) => {
						this.$message.error('链接失败');
					});
				} else { //修改课程
					console.log("修改课程cccc");
					console.log(JSON.parse(JSON.stringify(this.dialog.form)));
					this.$http.post("/admin/course",
						JSON.parse(JSON.stringify(this.dialog.form))
					).then((response) => {
						if(response.data.status == 200) {
							this.dialog.control = false;
							this.$message.success('修改成功');
							this.getCourses(this.currentNum);
						} else {
							this.$message.error('修改失败');
						}

					}, (response) => {
						this.$message.error('链接失败');
					});
				}

			},
			handleAvatarSuccess(res, file) {
				this.imageUrl = URL.createObjectURL(file.url);
			},
			beforeAvatarUpload(file) {
				const isLt2M = file.size / 1024 / 1024 < 4;
				if(!isLt2M) {
					this.$message.error('上传图片大小不能超过 4MB!');
				}
				return isLt2M;
			},
		},
	}
</script>

<style>
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	
	.avatar-uploader .el-upload:hover {
		border-color: #20a0ff;
	}
	
	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 300px;
		height: 169px;
		line-height: 178px;
		text-align: center;
	}
	
	.avatar {
		width: 300px;
		height: 169px;
		display: block;
	}
</style>


